export default {
	data() {

		return {
			word:[],
			id: '',
			secret: {},
			buylist: [], //订单列表
			goodsPrice: 0, //商品合计价格
			payPrice: 0, //商品合计原价
			payLing: 0, //优惠原价
			sumPrice: 0, //用户付款价格
			freight: 5, //运费
			note: '', //备注
			int: 1200, //抵扣积分
			deduction: 0, //抵扣价格
			disType: null, //优惠类型
			disPrice: null, //优惠金额
			tips: '',
			minprice: 0, //最小金额
			recinfo: {},
			// 快递方式
			items: [],
			// 选择的快递
			ship_name: '',
			// 快递费用
			shipmoney: '',
			choose: false,
			store:'',

		};
	},
	onLoad(option) {
		this.store=option.store;
		console.log(this.store)
		this.loadData();
		this.getAddress();
	},
	onShow() {
		uni.getStorage({
			key: 'selectAddress',
			success: (res) => {
				console.log('地址',res);
				this.recinfo = res.data;
				// this.loadData();
			}
		});
		
	},
	filters: {
		toFixed: function(x) {
			return parseFloat(x).toFixed(2);
		}
	},
	methods: {
		// 获取地址
		getAddress() {				
			this.$xm.post('/Order/getDefAdr/','', (res) => {
				console.log(res);
				this.recinfo = res;

			})
		},
		//加载购物车信息
		loadData() {
			let data={
				store:this.store
			}
			this.$xm.post('/Cart', data, res => {
				this.word=res.word;
				let goodsList = res.prolist;
				var payPrice = res.allprice;
				this.$xm.requestImg(goodsList);
				this.buylist = goodsList;
				this.minprice = Number(res.minprice)
				this.goodsPrice = res.allprice;
				this.tips = res.tips;
				this.payPrice = Number(payPrice);
				if (this.minprice > this.payPrice) {
					this.goodsPrice = this.payPrice + this.freight;
				}
				this.discounts(res.allprice);


			})
		},
		//获取优惠
		discounts(num) {
			let data = {					
				store: this.store,					
				money: num
			}
			this.$xm.post('/Coupon/match_optimals', data, res => {
				if (res.data) {
					this.sumPrice = res.data.money;
					var payLing = res.data.money;
					this.payLing = Number(payLing);
					if (this.minprice > this.payLing) {
						this.sumPrice = this.payLing + this.freight;
					}
					this.tips = res.data.msg;
					
					this.disPrice = res.data.cut;
					this.disType = res.data.type;
					this.getship()
				}

			})
		},
		// 获取快递方式
		getship() {
			if (this.recinfo) {
				let data = {						
					shipid: this.recinfo.id,						
					allprice: this.payLing,
					store:this.store
				}
				this.$xm.post('/Order/getShipMoney', data, (res) => {
					console.log(res);
					if (res.data !== undefined ) {
						if(res.data.constructor==Array){
							this.items = res.data;
							this.items.forEach(ele => {
								ele.checked = ''
							})
							this.items[0].checked = true;
							this.ship_name = this.items[0].name;
							this.shipmoney = this.items[0].price;
							this.freight = this.items[0].price;
						}else{
							this.items=[];
							this.freight = res.data.price;
							this.shipmoney = res.data.price;
							this.ship_name = res.data.name;
						}
						
					}

				})
			} else {
				uni.showToast({
					title: '请选择收件地址！',
					icon: false
				})
			}

		},
		//邮费切换
		shipChange: function(e) {
			this.ship_name = e.detail.value;
			let index;
			if (e.detail.value == '普通快递') {
				index = 0;
				this.shipmoney = 5;
			} else if (e.detail.value == '顺丰快递') {
				index = 1;
				this.shipmoney = 15;
			} else if (e.detail.value == '满59包邮') {
				index = 0;
				this.shipmoney = 0;

			}

			this.freight = this.items[index].price;
			// 原价
			this.goodsPrice = this.payPrice + this.freight;
			// 优惠价
			this.sumPrice = this.payLing + this.freight;

		},
		toPay() {
			let data = {					
				shipid: this.recinfo.id, //地址id
				shipmoney: this.shipmoney, //运费
				ship_name: this.ship_name, //物流方式名称
				memo: this.note, //备注
				couponid: '',
				store:this.store
			}
			this.$xm.post('/Order/saveOrder', data, (res) => {
				let data = res.data;
				if (res.s == 1) {
					uni.requestPayment({
						timeStamp: data.timeStamp,
						nonceStr: data.nonceStr,
						package: data.package,
						signType: data.signType,
						paySign: data.paySign,
						success: function(res) {
							console.log(res)
						},
						fail: function(res) {
							console.log(res)
						},
						complete: function(res) {
							if (res.errMsg == 'requestPayment:ok') {
								uni.showModal({
									title: '支付成功！',
									content: '',
									showCancel: false,
									confirmText: '确  定',
									confirmColor: '#2c2c2c',
									success: function(res) {
										uni.redirectTo({
											url: '../user/order_list/order_list?tbIndex=1'
										})
									}
								})
							} else {
								uni.showModal({
									title: '支付失败！',
									content: '',
									showCancel: false,
									confirmText: '确  定',
									confirmColor: '#2c2c2c',
									success: function(res) {
										if (res.confirm) {
											wx.redirectTo({
												url: '../user/order_list/order_list?tbIndex=0'
											})
										}
									}
								})
							}

						}
					});
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}
			})
		},
		//选择收货地址
		selectAddress() {
			uni.navigateTo({
				url: '../user/address/address?type=select'
			})
		}
	}
}
